.Main_Content_Container {
  display: flex;
  background-color: rgb(255, 255, 255);
  width: 230px;
  height: 345px;
  margin: 10px 10px 10px 10px;
}
.Main_Moive_Card {
  width: 100%;
}
.grid-container {
  display: grid;
  padding: 50px 100px;
  grid-template-columns: repeat(5, 1fr);
  grid-column-gap: 30px;
  justify-content: space-between;
}
/* .test {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
} */
body {
  background-color: rgb(0, 0, 0);
}
.Top {
  justify-content: center;
  display: flex;
}
.Header {
  height: 75px;
  background-color: rgb(0, 0, 0);
  display: flex;
  justify-content: space-between;
  margin: 0px 90px;
}
.HeaderLogo_container {
  max-width: 150px;
  max-height: 75px;
}
.Header_Logo {
  width: 100%;
  height: 100%;
}
.Button_Flex {
  display: flex;
  align-items: center;
}
.SignOut_Button {
  color: white;
  background-color: rgb(225, 14, 14);
}
.English_Button {
  color: white;
  background-color: black;
}
.English_Button p,
.SignOut_Button p {
  font-size: 19px;
  margin: 0;
  padding: 4px 0px 4px 0px;
}
.HeadRight_Button {
  padding: 5px 3px 5px 3px;
}
.MovieScreen_Container {
  /* background-color: rgb(74, 74, 74); */
  height: 500px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.MovieScreen_Container img {
  height: 500px;
  position: absolute;
  max-width: 1600px;
  width: 1350px;
}

h3 {
  margin: 0;
  padding: 0;
}

.MovieScreen_OutBox {
  width: 800px;
  height: 500px;
  /* background-color: rgb(193, 20, 20);2222 */
  display: flex;

  align-items: center;
  position: relative;
  z-index: 2;
}
.MovieScreen_title {
  color: aliceblue;
  font-size: 30px;
  padding: 5px 1px;
  margin: 5px 19px;
}
.MovieScreen_genres {
  color: rgb(68, 187, 62);
  font-size: 15px;
  padding: 10px 20px;
  display: flex;
  list-style-type: none;
}
.MovieScreen_genres li {
  margin: 0px 4px;
  color: rgb(230, 134, 9);
}
.MovieScreen_summary {
  color: rgb(197, 197, 197);
  font-size: 15px;
  padding: 10px 20px;
  margin: 0px 3px;
  overflow: hidden;
  height: 100px;
}
.MovieScreen_Out_image {
  height: 500px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 43;
}
.MovieScreen_Out_image img {
  width: 300px;
  height: 400px;
}
.MovieScreen_OutText {
  width: 600px;
}
.MovieScreen_year_rating_runtime {
  display: flex;
  margin: 0px 19px;
}
.MovieScreen_year_rating_runtime h3 {
  margin: 0px 3px 0px 3px;
  padding: 3px;
  color: aliceblue;
  border: 0.1px solid white;
  background-color: black;
  font-size: 15px;
}
